.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

legend {
  background-color: grey;
  color: white;
  padding: 5px;
}

.navbar h1{
  font-family: verdana;
  text-align: center;
  text-transform: uppercase;
}

fieldset {
  margin-top: 10px;
  margin-bottom: 50px;
}

#water {
  text-align: center;
  text-transform: uppercase;
  text-shadow: 10rem;
  text-decoration: solid;
}